import {
    NumberControlDescription,
    EnumControlDescription,
    BooleanControlDescription,
    StringControlDescription,
    ColorControlDescription,
    FusedNumberControlDescription,
    SegmentedEnumControlDescription,
    ImageControlDescription,
    FileControlDescription,
} from "./PropertyControls"

/** @beta */
export type ActionControlDescription<P = any> =
    | NumberControlDescription<P>
    | EnumControlDescription<P>
    | BooleanControlDescription<P>
    | StringControlDescription<P>
    | ColorControlDescription<P>
    | FusedNumberControlDescription<P>
    | SegmentedEnumControlDescription<P>
    | ImageControlDescription<P>
    | FileControlDescription<P>

/** @beta */
export type ActionControls<ActionProps = any> = {
    [K in keyof ActionProps]?: ActionControlDescription<Partial<ActionProps>>
}

/** @beta */
export type ActionHandler = (...args: any[]) => void

/**
 * Action hooks are picked up by Framer X
 * @param options - object containing action options
 * @returns event handler
 * @beta
 */
export type Action<T extends object = { [key: string]: any }> = (options: T) => ActionHandler

/** @private */
const actionControlsKey = Symbol()

/**
 * @beta
 */
export interface ActionInfo {
    controls: ActionControls
    title?: string
}

/**
 * Provide a title and controls for an action, used by Framer X
 * @param action - a reference to an {@link Action}
 * @param title - the display title of the action
 * @param controls - the action controls
 * @beta
 */
export function addActionControls<T extends object = { [key: string]: any }>(
    action: Action<T>,
    title: string,
    controls: ActionControls<T>
) {
    const info: ActionInfo = { title, controls }
    action[actionControlsKey] = info
}

/**
 * Retrieve the title and controls of an action
 * @param action - a reference to an {@link Action}
 * @returns an object containing the title and controls, or undefined
 * @beta
 */
export function getActionControls(action: Action): ActionInfo | undefined {
    return action[actionControlsKey]
}
